
.chart {
  width: 660px;
  margin: 0 auto;
  float: left;
}
.line {
  width: 518px;
  float: right;
}
.line .chart-container .title, .line .chart-container .sub-title, .line .chart-container .uppercase {
    display: none;
}
.line .chart-container .frappe-chart, .line .chart-container .graphics {
    margin-top: 2px;
    padding: 0;
}



